* {
    margin: 0px;
    padding: 0px;
}
body{
    justify-content: center;
    align-items: center;
}
.footer{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
}

p{
    font-size: 20px;
}
.button {
    align-items: center;
    background-color: initial;
    background-image: linear-gradient(#464d55, #25292e);
    border-radius: 8px;
    border-width: 0;
    box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1), 0 3px 6px rgba(0, 0, 0, 0.05);
    box-sizing: border-box;
    color: #fff;
    cursor: pointer;
    display: inline-flex;
    flex-direction: column;
    font-family: expo-brand-demi,system-ui,-apple-system,BlinkMacSystemFont,"Segoe UI",Roboto,"Helvetica Neue",Arial,"Noto Sans",sans-serif,"Apple Color Emoji","Segoe UI Emoji","Segoe UI Symbol","Noto Color Emoji";
    font-size: 18px;
    height: 52px;
    justify-content: center;
    line-height: 1;
    margin: 0;
    outline: none;
    overflow: hidden;
    padding: 0 32px;
    text-align: center;
    text-decoration: none;
    transform: translate3d(0, 0, 0);
    transition: all 150ms;
    vertical-align: baseline;
    white-space: nowrap; }
    .button:hover {
      box-shadow: rgba(0, 1, 0, 0.2) 0 2px 8px;
      opacity: .85; }
    .button:active {
      outline: 0; }
    .button:focus {
      box-shadow: rgba(0, 0, 0, 0.5) 0 0 0 3px; }
  
.text-link {
    text-decoration: none;
    color: whitesmoke;
    margin: 10px;
    justify-content: center;
    font-size: 20px;
}
.text-link:hover{
    color: rgb(235, 97, 150);
}
.container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center; 
}

.itemDetail{
    align-items: center;
    justify-content: center;
    display: flex;
    flex-direction: column;
    width: 50vw;
    height: auto;

}
